<template>
  <b-row>
    <b-col sm="12">
      <div class="d-flex">
        <h1>Orders history</h1>
        <b-button variant="success" size="sm" class="ml-auto mb-auto mt-auto" @click="$router.push({name:'Create Order'})">Create Order</b-button>
      </div>
      <div>
          <b-row>
              <b-col sm="4" >
                <label>Select Date From</label>
                <b-form-datepicker id="dateFrom" v-model="date.from" class="mb-2"></b-form-datepicker>
              </b-col>
              <b-col sm="4" >
                <label>Select Date To</label>
                <b-form-datepicker id="dateTo" v-model="date.to" class="mb-2"></b-form-datepicker>
              </b-col>
              <b-col sm="2">
                  <label>Select Status</label>
                  <b-form-select :options="statusOptions" v-model="date.status" text-field="label" value-field="value"></b-form-select>
              </b-col>
              <b-col sm="2">
                  <label>Click to search</label>
                  <b-button @click="search(date.from, date.to, date.status)" size="sm" variant="info" class="w-100">
                      <CIcon name="cil-search" />
                  </b-button>
              </b-col>
          </b-row>
      </div>
      <b-card>
        <b-table  striped hover :items="items.data" :fields="fields" :busy="loading.main">
          <!--loading-->
          <template #table-busy>
            <div class="text-center text-danger ">
              <b-spinner class="align-middle" variant="primary" type="grow"></b-spinner>
            </div>
          </template>

          <template v-slot:cell(price) = row>
            <b-input-group class="d-flex" style="flex-wrap:nowrap">
              <b-form-input size="sm" type="text" v-model="row.item.price" style="width:4rem"></b-form-input>
              <b-input-group-append>
                <b-button variant="success" size="sm" class="small-btn" @click="editPrice(row.item)">
                  <b-spinner v-if="loading.price == row.item.id" type="grow" small ></b-spinner>
                  <span v-else>Edit</span>
                </b-button>
              </b-input-group-append>
            </b-input-group>
          </template>

          <!--crated at-->
          <template v-slot:cell(created_at) = row >
            <span>{{ row.item.created_at | moment("from", "now") }}</span>
          </template>


          <!--status-->
          <template v-slot:cell(status) = row >
            <div>
              {{statusOptions.find(el=>{return el.value == row.item.status}).label}}
            </div>
          </template>

          <!--description-->
          <template v-slot:cell(description) = row>
            <!--to add dots for long description-->
            <span :title="row.item.description">
              {{`${row.item.description.substring(0, 10)}`}}<span v-if="row.item.description.length > 10">..</span>
            </span>
            
          </template>
          <!--actions-->
          <template v-slot:cell(actions) = row >
              <div class="d-flex">
    <!--details btn-->
                  <b-button
                    variant="primary"
                    size="sm"
                    :disabled="row.item.status == 'completed'"
                    class="ml-1 mr-1 small-btn"
                    @click="$router.push({name:'Create Packge', query:{orderId:row.item.id}})"
                    >Assign</b-button>
                    
                  <b-button
                    variant="info"
                    size="sm"
                    class="ml-1 mr-1 small-btn"
                    @click="getDetails(row.item.id)"
                    >details</b-button>
                  
                  <!--edit btn-->
                  <b-button
                    variant="success"
                    size="sm"
                    class="ml-1 mr-1 small-btn"
                    @click="$router.push({name:'Edit Order', params:{id:row.item.id}})"
                    >Edit</b-button>

                  <!--delete btn-->
                  <b-button
                    :disabled="loading.delete == row.item.id"
                    variant="danger"
                    size="sm"
                    class="ml-1 mr-1 small-btn"
                    @click="$bvModal.show(`${row.item.id}`)">
                    <span v-if="loading.delete != row.item.id">Delete</span>
                    <b-spinner v-else type="grow" small></b-spinner>
                  </b-button>
              </div>
              
              <!--delete modal-->
              <b-modal :id="`${row.item.id}`" centered title="Delete" ok-variant="danger" ok-title="Yes" cancel-title="No" @ok="deleteItem(row.item.id)">
                <div>Are you sure that you want to delete this?</div>
              </b-modal>
              
              <!--detals-->
              <b-modal ok-variant="success" ok-title="Edit" @ok="$router.push({name:'Edit Order', params:{id:row.item.id}})" :id="`details-${row.item.id}`" centered  size="xl" title="Details">
                  <b-row>
                    <b-col sm="12" v-if="loading.details"  class="text-center">
                      <b-spinner  type="grow" variant="primary"></b-spinner>
                    </b-col>

                    <template  v-if="!loading.details">
                      <b-col sm="6">
                        <b-img style="max-height:25rem;object-fit: contain;" class="w-100" v-for="img in details.image" :key="img.id" :src="img.image_path" :title="details.title"></b-img>
                      </b-col>
                      <b-col sm="6" class="d-flex align-items-center">
                        <b-row>
                          <b-col sm="4" class="text-right text-gery">
                              Title:
                          </b-col>
                          <b-col sm="8" class="h2 mb-0">
                              {{details.title}}
                          </b-col>

                          <b-col sm="4" class="text-right text-gery">
                              Price:
                          </b-col>
                          <b-col sm="8" class="h3 mb-0">
                              {{details.price}}
                          </b-col>

                          <b-col sm="4" class="text-right text-gery">
                              Description:
                          </b-col>
                          <b-col sm="8">
                              {{details.description}}
                          </b-col>



       


                          <b-col sm="4" class="text-right text-gery">
                              Status:
                          </b-col>
                          <b-col sm="8">
                            <span
                              :class="{'text-success':details.status == 'completed',
                              'text-info':details.status == 'processing',
                              'text-warning': details.status == 'review'}"
                              >{{details.status}}</span>
                          </b-col>

                          <b-col sm="4" class="text-right text-gery">
                              Payment Method:
                          </b-col>
                          <b-col sm="8">
                            {{details.payment_method.title}}
                          </b-col>


                          <b-col sm="4" class="text-right text-gery">
                              Created At:
                          </b-col>
                          <b-col sm="8">
                              {{details.created_at | moment('from', 'now')}}
                          </b-col>
                        </b-row>
                      </b-col>


    
                      <b-col sm="6">
                        <b-card class="mt-4">
                          <template #header>
                            <h3>User Data</h3>
                          </template>
                          <b-row v-if="details.user">
                            
                            <b-col sm="5" class="text-right text-gery">
                                Name:
                            </b-col>
                            <b-col sm="7">
                                {{`${details.user.first_name} ${details.user.last_name}`}}
                            </b-col>

                            <b-col sm="5" class="text-right text-gery">
                                Phone:
                            </b-col>
                            <b-col sm="7">
                               {{`(+${details.user.phone_code}) ${details.user.phone}`}}
                            </b-col>

                            <b-col sm="5" class="text-right text-gery">
                                Email:
                            </b-col>
                            <b-col sm="7">
                               {{details.user.email}}
                            </b-col>

                            <b-col sm="5" class="text-right text-gery">
                                Location:
                            </b-col>
                            <b-col sm="7">
                               {{`${details.user.country.name}, ${details.user.state.name}, ${details.user.city.name}`}}
                            </b-col>


                          </b-row>
                        </b-card>
                      </b-col>

 
                      <b-col sm="6">
                        <b-card class="mt-4">
                          <template #header>
                            <h3>Recipient Data</h3>
                          </template>
                          <template v-for="rec in details.recipient">
                            <b-row :key="rec.id">

                              <b-col sm="5" class="text-right text-gery">
                                  Name:
                              </b-col>
                              <b-col sm="7">
                                  {{`${rec.first_name} ${rec.last_name}`}}
                              </b-col>

                              <b-col sm="5" class="text-right text-gery">
                                  Phone:
                              </b-col>
                              <b-col sm="7">
                                {{`(+${rec.phone_code}) ${rec.phone}`}}
                              </b-col>

                              <b-col sm="5" class="text-right text-gery">
                                  Address:
                              </b-col>
                              <b-col sm="7">
                                  {{rec.address}}
                              </b-col>

                              <b-col sm="5" class="text-right text-gery">
                                  Location:
                              </b-col>
                              <b-col sm="7">
                                {{`${rec.country.name}, ${rec.state.name}, ${rec.city.name}`}}
                              </b-col>
                            </b-row>

                          </template>
                          
                        </b-card>
                      </b-col>
                    </template>

                  </b-row>
              </b-modal>
          </template>

        </b-table>
        <div class="overflow-auto d-flex" v-if="$route.query.page">
            <b-pagination-nav class="m-auto" @change="getData($route.query.page)" :link-gen="linkGen" :number-of-pages="items.to" use-router></b-pagination-nav>
        </div>
      </b-card>
    </b-col>
  </b-row>
</template>

<script>
import Vue from 'vue'
Vue.use(require('vue-moment'));
import axios from "axios"
import {showToast } from '@/assets/js/index';
export default {
  name: 'OrdersHistory',
  data() {
    return {

      statusOptions:[
        {
          label:"All",
          value:"all"
        },
        {
          label:"Review",
          value:"review"
        },
        {
          label:"Pending Payment",
          value:"pending_payment"
        },
        {
          label:"Processing",
          value:"processing"
        },
        {
          label:"Shipped",
          value:"shipped"
        },
        {
          label:"Completed",
          value:"completed"
        }
      ],
      details:{payment_method:{}},
      date:{
          from:'',
          to:'',
          status:'all'
      },
      loading:{
        main:false,
        price:0,
        delete:0,
        details:false
      },
      fields:[
        {key:"title"},
        {key:"price"},
        {key:"description"},
        {key:"status"},
        {key:"created_at", label:"Date"},

        { key: "actions", label: "Actions" }
      ],
      items: {
          data:[]
      },
    }
  },
  mounted() {
    this.date.status = this.$route.params.status;
    
    //get default data
    let currentMonth   = new Date().getMonth() + 1;
    let prevMonth      = new Date().getMonth();
    let currentDay     = new Date().getDay();
    let currentYear    = new Date().getUTCFullYear()
    if(currentMonth.length < 2) {
      currentMonth = String('0'+currentMonth)
    }
    if(prevMonth.length < 2) {
      prevMonth = String('0'+prevMonth)
    }
    this.date.from = `${currentYear}-${prevMonth}-${currentDay}`
    this.date.to   = `${currentYear}-${currentMonth}-${currentDay}`
    setTimeout(()=>{
      this.search(`${currentYear}-${prevMonth}-${currentDay}`,`${currentYear}-${currentMonth}-${currentDay}`, this.date.status)
    },200)
    
  },
  methods:{
    //search
    search(from,to,status) {

 
        // if(!this.date.from || !this.date.to) {
        //   this.getData(1)

        //   this.$router.push({ name: 'Orders History', query: { page: 1}})
        // } else {
        //   if(this.date.status != 'all') {
        //       status = this.date.status
        //       url = `/admin/v1/orders/date/between/${this.date.from}/${this.date.to}/${status}`
        //   } else {
        //       url = `/admin/v1/orders/date/between/${this.date.from}/${this.date.to}`
        //   }
        //   this.loading.main = true
        //   //this.$router.push({ name: 'Orders History'})
        //   axios.get(url)
        //   .then(res=>{
        //       this.items.data = res.data.data;
        //       this.loading.main = false
        //   })
        //   .catch(()=>{
        //       this.loading.main = false
        //   })
        // }
          // if(this.date.status != 'all') {
          //     status = this.date.status
          //     url = 
          // } else {
          //     url = `/admin/v1/orders/date/between/${this.date.from}/${this.date.to}`
          // }
          // this.loading.main = true
           this.loading.main = true
           let st = ''
           if (status != 'all'){
             st = `/${status}`
           }
          axios.get(`/admin/v1/orders/date/between/${from}/${to}${st}`)
          .then(res=>{
              this.items.data = res.data.data;
              this.loading.main = false
          })
          .catch(()=>{
              this.loading.main = false
          })

    },
    getData(page) {
      this.loading.main = true
      axios.get(`/admin/v1/orders?page=${page}`).then((res) => {
        this.loading.main = false;

        this.items = res.data.data
      })
      .catch((error) => {
        this.loading.main = false;
        console.error(error)
      })
    },
      linkGen(pageNum) {
        return pageNum === 1 ? '?' : `?page=${pageNum}`
      },
    getDetails(id) {
      this.loading.details = true
      this.$bvModal.show(`details-${id}`)
      axios.get(`admin/v1/orders/${id}`)
      .then(res=>{
        this.loading.details = false
        this.details  = res.data.data
      })
      .catch(err=>{
        this.loading.details = false
        console.log(err.response)
      })
    },
  editPrice(item) {
    this.loading.price = item.id
    axios.put(`admin/v1/orders/${item.id}`, {status:item.status, price:item.price})
    .then(()=>{
      this.loading.price = ''
      showToast("success", "Success", `item price has been updated successfully`, this)
    })
    .catch(err => {
       this.loading.price = ''
        for(let error in err.response.data.errors) {
            showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
        }
        showToast('danger', `Error`, `${err.response.data.error}`, this)
    })
  },



    deleteItem(id){
      this.loading.delete = id
      axios.delete(`/admin/v1/orders/${id}`)
      .then(()=>{
        showToast("success", "Success", "Item deleted successfully!", this)
        //remove deleted item
        this.loading.delete = 0
        this.items = this.items.filter(item=>{
          return item.id != id
        })
      })
      .catch(err=>{
          this.loading.delete = 0;
          for(let error in err.response.data.errors) {
              showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
          }
          console.log(err.response)
      })
    }
  },

 

}
</script>
<style scoped>
.text-gery {
    color:#999
}
.small-btn {
  width: 4rem!important;
}
.b-skeleton:hover {
  cursor: none;
}
.center-middle {
  justify-content: center;
  align-items: center;
}
</style>